.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 5rem;
    background-color: #04a63d;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10%;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    z-index: 10;
}

.main-image {
    width: 100%;
    height: 30rem;
    z-index: 0;
    overflow: hidden;
}

.main-image img {
    width: 107%;
    height: 100%;
    object-fit: cover;
    transform: rotateZ(-5deg) translateY(-1rem) translateX(-1rem);
}

@media (width > 400px) and (width <= 778px) {
    h1 {
        font-size: 20px;
    }
}

@media (width <= 400px) {
    h1 {
        font-size: 15px;
    }
}