.wrapper {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.button {
  cursor: pointer;
  font: inherit;
  border: none;
  background-color: #075c1f;
  color: white;
  padding: 0.75rem 3rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 25px;
  font-weight: bold;
  margin-right: 15px;
  text-decoration: none;
}

.button:last-child {
  margin-right: 0px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.button:hover,
.button:active {
  background-color: #053f15;
}

.icon {
  width: 1.35rem;
  height: 1.35rem;
  margin-right: 0.5rem;
}

.badge {
  margin: -20px -50px auto auto;
  background-color: #ed3232;
  border-radius: 50%;
  font-weight: bold;
  height: 25px;
  width: 25px;
  font-size: 13px;
  padding: 3px;
  box-shadow: 0px 1px 4px 0px rgb(255, 0, 0, 0.9);
  margin-left: 1.5rem;
}

.button:hover .badge,
.button:active .badge {
  background-color: #a61b1b;
}

.bump {
  animation: bump 300ms ease-out;
}

@keyframes bump {
  0% {
    transform: scale(1);
  }

  10% {
    transform: scale(0.7);
  }

  30% {
    transform: scale(1.4);
  }

  50% {
    transform: scale(0.7);
  }

  100% {
    transform: scale(1);
  }
}

@media (width >= 500px) and (width <= 778px) {
  .button {
    font-size: 12px;
    padding: 5px 10px;
  }

  .icon {
    width: 1rem;
    height: 1rem;
    margin-right: 0.5rem;
  }

  .badge {
    margin: -15px -20px auto auto;
    padding: 3px;
    font-size: 13px;
  }

  .cartBtnText {
    margin-right: 0.5rem;
  }
}

@media (width < 500px) {
  a {
    display: inline-block;
  }

  .button {
    font-size: 5px;
    padding: 5px;
  }

  .badge {
    margin: -10px -13px auto auto;
    font-size: 10px;
    height: 20px;
    width: 20px;
  }

  .icon {
    width: 1.5rem;
    height: 1.5rem;
    margin-right: 0px;
    padding: 5px 3px 0px 3px;
  }
}
